.test {
  display: flex;
  font-size: 100px;
}
* {
  margin: 0;
  padding: 0;
}
main {
  width: 100%;
  height: 100vh;
  display: grid;
  place-items: center;
}
#modalOpen {
  /* display: none; */
  background-color: coral;
  color: white;
  border: none;
  padding: 20px 40px;
  border-bottom: 7px solid rgb(237, 111, 65);
  box-shadow: 0px 3px 4px black;
  cursor: pointer;
  font-size: 23px;
  transition: all 0.2s;
}

#modalOpen:hover {
  margin-top: 5px;
  border-bottom: 2px solid rgb(237, 111, 65);;
}

#modal {
  display: none;
  position: fixed;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  background-color: gray;
  z-index: 10;
}

.modal-content {
  background-color: #f4f4f4;
  margin: 15% auto;
  width: 50%;
  box-shadow: 0 2px 7px balck;
  animation-name: popup;
  animation-duration: 1.1s;
  height: 100px;
  overflow-y: scroll;
}

.modal-header {
  background-color: aqua;
  padding: 3px 15px;
  display: flex;
  justify-content: space-between;
}

@keyframes popup {
  from {
    transform: translateY(150%);
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.modalClose {
  font-size: 1.3rem;
  cursor: pointer;
}